<template>
	<div class="Pagination clearfix" v-if="total">
		<div class="pre dis">
			<div class="icon-box">
				<img src="">
			</div>
		</div>
		<div class="num">
			<span class="num-txt">1</span>
		</div>
		<div class="num" v-if="ellipsis[0]">
			<span class="num-txt">...</span>
		</div>
		<div class="num" v-for="(num,index) in showNums">
			<span class="num-txt">{{num}}</span>
		</div>
		<div class="num" v-if="ellipsis[1]">
			<span class="num-txt">...</span>
		</div>
		<div class="num" v-if="pages>1">
			<span class="num-txt">{{pages}}</span>
		</div>
		<div class="next">
			<div class="icon-box">
				<img src="">
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			page: {
				type: Number,
				// required: true,
				default(){
					return 5
				}
			},
			total: {
				type: Number,
				// required: true,
				default: 66
			},
			limit: {
				type: Number,
				default: 6
			}
		},
		computed: {
			pages() {
				return Math.ceil(this.total/this.limit)
			},
			showNums(){
				let btns=[];
				//小于10页显示全部页码
				if( this.pages <= 10 ) {
					btns=[2,this.pages-1]
				}else{
					const aboutLimit = this.btn_limit - Math.floor(this.btn_limit/2);
					if( this.page <= aboutLimit + 1 ) {
						btns.push=[2,this.btn_limit]
					}else if(this.page >= this.pages - this.btn_limit){
						btns.push=[this.pages - this.btn_limit, this.pages-1]
					}else{
						btns.push =
						[this.page - aboutLimit, this.page - aboutLimit + this.btn_limit]
					}
				}
				return btns.forEach(item => {
					let btns = [];
					for(let i=item[0];i<item[1];i++){
						btns.push(i)
					}
				})
			},
			ellipsis(){
			    return	[this.showNums[0]==2, this.showNums[showNums.length-1] == this.pages-1]
			}
		},
		data() {
			return {
				btn_limit: 5,
				show_all_threshold: 10
			};
		}
	}
</script>

<style lang="scss" scoped>
	.Pagination{
		padding-left: 1px;
		.pre,.next{
			padding: 0 5px;
			.icon-box{
				width: 10px;
				height: 10px;
				background: #000066;
				img{
					display: block;
				}
			}
		}
		.btn{
			height: 12px;
			padding: 0 5px;
			.num-txt{
				color: #1C1D1D;
				cursor: pointer;
				&.now{
					color: #1CD7D1;
				}
			}
		}
	}
</style>
